import React, {Component} from 'react';
import {Icon, Form, Button, Radio, DatePicker, Input, Upload, Transfer, Modal, InputNumber, Avatar } from "antd";
import {connect} from "react-redux";
import {actionCreators} from '../store';
import moment from "moment";
import "./index.scss";
import MultiSelectGonghao from '../components/MultiSelectGonghao';

class EditMessage extends Component {
  state = {
    selectGonghaoModal: false
  };

  componentWillMount() {
    this.props.getAllCodes();
    let query = this.props.history.location.query;
    query && this.props.initKefuMessageInfo(query);
    query && this.setState({selectGonghaoModal: false})
  }

  render() {
    const { kefuMessageInfo, content, paramsModal, changeContentValue, changeMessageValue, uploadImg, addMessage, selectMessage, hideParamsModal, submitMessage, allCodes, targetKeys, changeGonghao, selectParams, resetState, history } = this.props;
    const {selectGonghaoModal} = this.state;
    const mapMaterials = kefuMessageInfo.contents.map((item, index) => (
      <li key={item._id || index} onClick={() => selectMessage(item, index)}>
        <div className="material-item">
          <div className="material-title fl textEllipsis">{item.title || "标题"}</div>
          <div className="material-img fl">
            <img src={item.picurl} alt=""/>
          </div>
        </div>
      </li>
    ));
    let isImgAndText, FormItemWrapper;
    if (kefuMessageInfo.type === 0) {
      isImgAndText = <div className="show-material-wrapper fr">
        <ul>
          {mapMaterials}
          {kefuMessageInfo.contents.length < 8 && <li className="add-material tc" onClick={addMessage}><Icon type="plus"/></li>}
        </ul>
      </div>
    }
    let isUploadImg, isTextUploadImg;
    if (content.picurl) {
      isTextUploadImg = <img style={{width: "100px"}} src={content.picurl} alt=""/>
    } else {
      isTextUploadImg = <Button><Icon type="upload"/> 上传图片 </Button>
    }
    if (kefuMessageInfo.img) {
      isUploadImg = <img style={{width: "100px"}} src={kefuMessageInfo.img} alt=""/>
    } else {
      isUploadImg = <Button><Icon type="upload"/> 上传图片 </Button>
    }
    if (kefuMessageInfo.type === 1) {
      FormItemWrapper = <Form.Item label="详细信息">
        <Input.TextArea value={content.description} placeholder="请输入客服消息详细信息" onChange={e => changeContentValue(e, "description")}/>
      </Form.Item>
    } else if (kefuMessageInfo.type === 0) {
      FormItemWrapper = <div>
        <Form.Item label="图片">
          <Upload name="imageFile" action="/reply/upload" onChange={(info) => {
            uploadImg(kefuMessageInfo, info)
          }} showUploadList={false}>
            {isTextUploadImg}
          </Upload>
        </Form.Item>
        <Form.Item label="标题">
          <Input placeholder="请输入标题" value={content.title} onChange={e => changeContentValue(e, "title")}/>
        </Form.Item>
        <Form.Item label="链接">
          <Input placeholder="请输入链接" value={content.url} onChange={e => changeContentValue(e, "url")}/>
        </Form.Item>
      </div>
    } else if (kefuMessageInfo.type === 2) {
      FormItemWrapper =
        <Form.Item label="图片">
          <Upload name="imageFile" action="/reply/upload" onChange={(info) => {
            uploadImg(kefuMessageInfo, info)
          }} showUploadList={false}>
            {isUploadImg}
          </Upload>
        </Form.Item>;
    }
    const formItemLayout = {
      labelCol: {
        sm: {span: 4}
      },
      wrapperCol: {
        sm: {span: 8, offset: 0}
      },
      colon: false
    };
    let Channel;
    if(kefuMessageInfo.channel === 1) {
      Channel = <><Form.Item label="延时时间(分钟)">
        <InputNumber value={kefuMessageInfo.delay} min={1} onChange={value => changeMessageValue(value, "delay")} />
      </Form.Item>
        <Form.Item label="互动类型">
          <Radio.Group value={kefuMessageInfo.action_type} onChange={e => changeMessageValue(e.target.value, "action_type")}>
            <Radio value={0}>全部</Radio>
            <Radio value={1}>用户关注</Radio>
            <Radio value={3}>用户回复</Radio>
            <Radio value={2}>点击菜单</Radio>
          </Radio.Group>
        </Form.Item>
        </>
    } else if(kefuMessageInfo.channel === 2) {
      Channel =
        <Form.Item label="定时时间">
          <DatePicker showTime placeholder="选择时间" value={moment(kefuMessageInfo.timing_time)} onChange={(value, dateString) => changeMessageValue(dateString, 'timing_time')} />
        </Form.Item>
    }
    return (
      <div>
        <div className="container edit-message">
          {isImgAndText}
          <div className="kefu-form fl">
            <Form {...formItemLayout} id="gonghao">
              {/*<Form.Item label="选择公号">*/}
                {/*<Avatar onClick={() => this.setState({selectGonghaoModal: true})} className="cursor" shape="square" icon="user" />*/}
              {/*</Form.Item>*/}
              <Form.Item label="消息类型">
                <Radio.Group value={kefuMessageInfo.type} onChange={e => changeMessageValue(e.target.value, "type")}>
                  <Radio.Button value={1}>文本</Radio.Button>
                  <Radio.Button value={0}>图文</Radio.Button>
                  <Radio.Button value={2}>图片</Radio.Button>
                </Radio.Group>
              </Form.Item>
              {FormItemWrapper}
              <Form.Item label=" ">
                <Button type="primary" onClick={selectParams}>选择其他参数</Button>
              </Form.Item>
            </Form>
          </div>
        </div>
        <Modal width={1000} visible={selectGonghaoModal} maskClosable={false} closable={false} mask={false}  footer={<Button type="primary">确定</Button>}>
          <MultiSelectGonghao />
        </Modal>
        <Modal
          title="选择其他参数"
          visible={paramsModal}
          onOk={() => submitMessage(kefuMessageInfo, history)}
          onCancel={hideParamsModal}
          okText="确认"
          cancelText="取消"
          className="params-modal"
          style={{top: 70}}
        >
          <Transfer
            dataSource={allCodes}
            showSearch
            targetKeys={targetKeys}
            onChange={changeGonghao}
            render={item => item.nick_name}
            rowKey={record => record.code}
            className="gonghao-transfer fl"
          />
          <Form className="params-form fl" colon={false} labelCol={{sm: {span: 4}}} wrapperCol={{sm: {span: 20, offset: 0}}}>
            <Form.Item label="备注">
              <Input style={{width: 300}} value={kefuMessageInfo.remarks} placeholder="请输入备注信息，方便区分" onChange={e => changeMessageValue(e.target.value, "remarks")} />
            </Form.Item>
            <Form.Item label="发送方式">
              <Radio.Group value={kefuMessageInfo.channel} onChange={e => changeMessageValue(e.target.value, "channel")}>
                <Radio.Button value={0}>人工</Radio.Button>
                <Radio.Button value={1}>延时</Radio.Button>
                <Radio.Button value={2}>定时</Radio.Button>
              </Radio.Group>
            </Form.Item>
            {Channel}
            <Form.Item label="接收用户">
              <Radio.Group value={kefuMessageInfo.sex} onChange={e => changeMessageValue(e.target.value, "sex")}>
                <Radio value="all">全部</Radio>
                <Radio value="1">男</Radio>
                <Radio value="2">女</Radio>
                <Radio value="0">未知</Radio>
              </Radio.Group>
            </Form.Item>
          </Form>
        </Modal>
      </div>
    )
  }
  componentWillUnmount() {
    this.props.resetState()
  }
}

const mapState = state => ({
  kefuMessageInfo: state.message.kefu.kefuMessageInfo,
  content: state.message.kefu.content,
  paramsModal: state.message.kefu.paramsModal,
  allCodes: state.message.kefu.allCodes,
  targetKeys: state.message.kefu.targetKeys
});

const mapDispatch = dispatch => ({
  initKefuMessageInfo(query) {
    dispatch(actionCreators.initKefuMessageInfo(query));

  },
  changeContentValue(e, name) {
    dispatch(actionCreators.changeContentValue(e.target.value, name));
  },
  uploadImg(kefuMessageInfo, info) {
    if (info.file.status === 'done') {
      let img = `http://t.dmmup.com//uploads/${info.file.response.filename}`,
        img_path = `./public/uploads/${info.file.response.filename}`;
      if (kefuMessageInfo.type === 0) {
        dispatch(actionCreators.changeContentValue(img, "picurl"));
      } else {
        dispatch(actionCreators.changeMessageValue(img, "img"));
        dispatch(actionCreators.changeMessageValue(img_path, "img_path"));
      }
    }
  },
  changeMessageValue(value, name) {
    dispatch(actionCreators.changeMessageValue(value, name));
  },
  addMessage() {
    dispatch(actionCreators.addMessage());
  },
  selectMessage(info, index) {
    dispatch(actionCreators.selectMessage(info, index));
  },
  resetState() {
    dispatch(actionCreators.resetState());
  },
  selectParams() {
    dispatch(actionCreators.selectParams());
  },
  hideParamsModal() {
    dispatch(actionCreators.hideParamsModal());
  },
  submitMessage(info, history) {
    dispatch(actionCreators.submitMessage(info, history));
  },
  changeGonghao(targetKeys) {
    dispatch(actionCreators.changeGonghao(targetKeys))
  },
  getAllCodes() {
    dispatch(actionCreators.getAllCodes())
  }
});

export default connect(mapState, mapDispatch)(EditMessage);